<template>
	<view class="content">
		<input style="height: 10%;" disabled="true"/>
		
		
		<swiper class="swiper"
		:autoplay="autoplay" 
		:duration="duration"
		:indicator-dots="true"
		:indicator-actice-color="actice_color"
		:indicator-color="noactive_color">
			<swiper-item>
				<view class="swiper-item">
					<view class="swiper-item-img">
						<image src="../../static/guide/1.png" mode="widthFix"></image>
					</view>
				</view>
			</swiper-item>
			<swiper-item>
				<view class="swiper-item_2" style="justify-content: center;">
					<view style="display: flex;flex-direction: column;color: #0dbbae;margin-left: 23rpx;">
						<text style="font-size: 28px;">
							独一无二
						</text>
						<text style="font-size: 14px;margin-top: 5px;">
							以积极思维促进音乐繁荣
						</text>
					</view>
					<view class="swiper-item-img" style="margin-top: 30rpx;">
						<image src="../../static/guide/2.png" mode="widthFix"></image>
					</view>
					
				</view>
				
			</swiper-item>
			<swiper-item>
				<view class="swiper-item_2" style="justify-content: center;">
		
					<view class="swiper-item-img" style="">
						<image src="../../static/guide/3.png" mode="widthFix"></image>
					</view>
					
					<view style="display: flex;flex-direction: column;color: #0dbbae;text-align: center;align-items: center;">
						<text style="font-size: 28px;">
							方便使用
						</text>
						<view style="width: 80%;text-align: center;display: flex;flex-direction: row;flex-wrap: wrap;">
							<text style="font-size: 14px;margin-top: 5px;">
								一个伟大的方式来产生所有的动机，你需要得到适合的方式
							</text>
						</view>
		
					</view>
					<input style="height: 10px;flex-shrink: 0;" disabled="true"/>
				</view>
				
			</swiper-item>
		</swiper>
		<input style="height: 30rpx;width: 100%;" disabled="true"/>
		
		<view style="display: flex;flex-direction: column;">
			<view style="display: flex;flex-direction: row;justify-content: space-around;">
				<button style="color: #fff;background-color: #43cf7c;width: 300rpx;border-radius: 30px;" @click="login()">登录</button>
				
				<button style="color: #44c8bf;background-color: #fff;width: 300rpx;border-radius: 30px;" @click="reg()">注册</button>
			</view>
			
			<view style="display: flex;flex-direction: row;justify-content: center;margin-top: 50rpx;">
				<text style="color: #44c8bf;font-size: 13px;" @click="youke()">游客游览</text>
			</view>
		</view>
		
		
	</view>
</template>

<script>
	export default {
		data() {
			return {
				background: ['color1', 'color2', 'color3'],
				autoplay: false,
				duration: 500,
				jumpover: '跳过',
				experience: '立即体验',
				actice_color:'#ff0000',
				noactive_color:'#949494',
				show_next:true
			}
		},
		methods: {
			change_next(){
				this.show_next=false;
			},
			youke(){
				uni.showToast({
					title:'暂不支持游客预览',
					icon:'none'
				});
			},
			login(){
				uni.redirectTo({
					url:'/pages/login/index'
				});
			},
			reg(){
				uni.redirectTo({
					url:'/pages/login/register'
				})
			},
			launchFlag: function(){
				/**
				 * 向本地存储中设置launchFlag的值，即启动标识；
				 */
				uni.setStorage({
					key: 'launchFlag',
					data: true,
				});
				uni.switchTab({
					url: '/pages/tabBar/component/component'
				});
				
			}
		}
	}
</script>
<style>
	page,
	.content{
		width: 100%;
		height: 100%;
		background-size: 100% auto ;
		padding: 0;
	}
	.swiper{
		width: 100%;
		height: 65%;
		background: #FFFFFF;
	}
	.swiper-item {
		width: 100%;
		height: 100%;
		text-align: center;
		position: relative;
		display: flex;
		/* justify-content: center; */
		align-items:flex-end;
		flex-direction:column-reverse;
		padding: 10px;
	}
	.swiper-item_2 {
		width: 100%;
		height: 100%;
		display: flex;
		flex-direction:column;
		padding: 10px;
	}
	.swiper-item-img{
		width: 100%;
		height: auto;
	}
	.swiper-item-img image{
		width: 100%;
	}
	
</style>
